import { Component, OnInit } from '@angular/core';
import { TiendaService } from 'src/app/services/services.index';
import { ProveedorModel } from 'src/app/model/tienda.model';
declare var swal: any;
@Component({
  selector: 'app-proveedor',
  templateUrl: './proveedor.component.html',
  styles: []
})
export class ProveedorComponent implements OnInit {
  proveedores: ProveedorModel[];
  Newproveedor: ProveedorModel;
  term = '';
  constructor(
    private _tiendaSrv: TiendaService,
  ) {
    this.Newproveedor = new ProveedorModel();
  }

  ngOnInit() {

    this.imprimirProveedores();
  }
  imprimirProveedores() {
    this._tiendaSrv.getAllProveedor().subscribe(res => {
      this.proveedores = res;

    });
  }

  GuardarOeditarProveedor(basicModal2) {
    this._tiendaSrv.saveOrUpdateProveedor(this.Newproveedor).subscribe(res => {
      swal('Bien!', 'Guardado!', 'success').then(() => {
        this.imprimirProveedores();
      });

    });
    basicModal2.hide();
    this.Newproveedor = new ProveedorModel();
  }
  editarProveedor(proveedor, basicModal2) {
    basicModal2.show();
    this.Newproveedor = proveedor;
  }
}
