import { Injectable } from '@angular/core';
import { ProveedorModel, ProductoModel } from '../model/tienda.model';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Injectable({
    providedIn: 'root'
})
export class TiendaService {

    constructor(private http: HttpClient) { }

    // =================== proveedor ===================== 
    // listar 
    public getAllProveedor(): Observable<ProveedorModel[]> {
        return this.http.get<ProveedorModel[]>('http://localhost:8080/listaProveedores',
            { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) });
    }
    // Guardar y editar
    public saveOrUpdateProveedor(proveedor: ProveedorModel): Observable<ProveedorModel> {
        return this.http.post<any>('http://localhost:8080/SaveOpdateProveedor',
            JSON.stringify(proveedor), { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) });
    }
    // ============= producto ==================== 
    // listar 
    public getAllProductor(): Observable<ProductoModel[]> {
        return this.http.get<ProductoModel[]>('http://localhost:8080/listaProductos')

    }
    // guardar y editar
    public saveOrUpdateProducto(producto: ProductoModel): Observable<ProductoModel> {
        // console.log(JSON.stringify(producto));
        return this.http.post<any>('http://localhost:8080/SaveOpdateProducto', JSON.stringify(producto),
            { headers: new HttpHeaders({ 'Content-Type': 'application/json' }) });
    }
}
